/* Home page*/

#main .page-container .home{
    margin-bottom: 70px;
    border-radius: 50px 50px 0 0;
}

/* actu*/

.news-container{
  position: relative;
  background-color: #fff;
  margin-top: 10px;
  border-radius: 50px 50px 0 0;
}
.news-content{
  margin: 0 auto;
  padding: 0 0 60px;
}
.news-bg{
  height: 100%;
  width: 100%;
  position: absolute;
  border-radius: 50px 50px 0 0;
}

.news-wrap{
  background: #fff;
  position: relative;
  border-radius: 50px 50px 60px 0;
  padding-top: 10px;
}

.page-container .home .news-container h1{
  color: rgb(56,56,56);
  text-align: left;
  font-size: 18px;
  z-index: 2;
  position: relative;
  font-weight: bold;
  font-family: GOTHICB, Krona;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  padding: 0 10px;
}
.page-container .home .news-container .news-link{
  float: right;
  font-size: 14px;
  box-shadow: 0px 3px 5px -1px rgb(0 0 0 / 20%), 0px 6px 10px 0px rgb(0 0 0 / 14%), 0px 1px 18px 0px rgb(0 0 0 / 12%);
  border-radius: 50%;
  padding: 5px;
  background: rgb(168 14 80 / 100%);
}
.page-container .home .news-container h1 a {
  color: rgb(56,56,56)!important;
  display: flex;
  align-items: center;
}



.page-container .home .news-container svg{
  width: 20px;
  height: 20px;
}

.home .post-entry{
  height: inherit;
  position: relative;
}


.home .post-link{
  height: 100%;
}
.home .post-thumbnail{
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  height: inherit;
  border-radius: 2px;
  position: relative;
}

.home .post-box h2{
  color: #fff;
  font-size: 25px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  width: 70%;
  text-shadow: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
  text-transform: uppercase;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
  
.home .post-box h2 a{
  color: #fff;
}
.home .post-box .fa-plus-circle{
  position: absolute;
  bottom: 20px;
  right: 10px;
  color: #fff;
  font-size: 18px;
}


