

 .share-btn {
    transform: translateY(100%);   
    bottom: 0;
    height: 150px;
    width: 100%;
    z-index: 9999;
    text-align: center;
    position: fixed;
    background: rgb(255, 255, 255);
    border-radius: 10px 50px 0px 0px;
    transition: all 0.5s ease-out;
}

.share-content{
    display: flex;
    flex-direction: row !important;
    align-items: center;
    justify-content: center;
    
}

 .share-btn.open {
    bottom: 0;
    transform: translateY(0);
    box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 2px rgb(0 0 0 / 24%);
}

 .share-btn button {
    margin: 10px;
}

.share-btn h2 {
    font-size: 12px;
}

.backgroundShare{
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(134, 132, 132, 0.3);
    z-index: 100;
}

.copyLink{
    display: flex;
 }
.shareLink{
    width: 70%;
    margin: 10px auto;
    border: none;
    background: #ccc;
    color: #fff;
    text-align: center;
    font-size: 14px;
    height: 40px;
}

.copyLinkBtn{
    background: rgb(56 56 56);
    color: white;
    width: 20%;
}

.copyLinkBtn.active{
    background: rgb(85, 84, 84);
    color: white;
    width: 20%;
}