/* Coiffures*/

.page-coiffures .coiffures-content {
    margin: 10px auto 70px;
}

.page-coiffures .coiffures-content .coiffure-img{
    height: 250px;
    width: 150px;
    flex: 1 1 auto;
} 

.page-coiffures .coiffures-content img{
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.image-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
.Coiffures .App-top{
    display: none;
}

.Coiffures .search-container {
    background: transparent; 
    display: flex;
    align-items: center;
}

.Coiffures .search-container input {
    background: rgba(242, 241, 249, 0.6) ;
}

.style-header.sticky {
    position: sticky;
    z-index: 999;
    top: 0;
    background: #fff;
}

.filterbtn.all-types {
    background: rgb(56 56 56);
    color: #fff;
    padding: 5px;
    border-radius: 50px;
    min-width: 60px;
}

.Coiffures .navbar-toggle {
    width: 5%;
}

.Coiffures .submitSearch {
    top: 10px;
}

.style-header.sticky {
    padding-bottom: 10px;
}

.Coiffures .search-container button.search {
    width: 80%;
}